import React, { useEffect, useRef, useState } from 'react';
import Chart from 'chart.js/auto';
import Papa from 'papaparse';

const ChartLink = () => {
  const chartRef = useRef(null);
  const [chartInstance, setChartInstance] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://raw.githubusercontent.com/Ayushkumawat/Portfolio/main/public/Connections.csv');
        const csvData = await response.text();

        const parsedData = Papa.parse(csvData, { header: true }).data;

        const monthlyConnections = parsedData.reduce((acc, item) => {
          const date = new Date(item['Connected On']);
          const month = `${date.toLocaleString('default', { month: 'short' })} ${date.getFullYear()}`;
          acc[month] = (acc[month] || 0) + 1;
          return acc;
        }, {});

        const labels = Object.keys(monthlyConnections);
        const data = Object.values(monthlyConnections);
        const minYValue = Math.min(...data);

        const ctx = chartRef.current.getContext('2d');

        if (chartInstance) {
          chartInstance.destroy();
        }

        const newChartInstance = new Chart(ctx, {
          type: 'line',
          data: {
            labels,
            datasets: [
              {
                label: 'LinkedIn Connections Made',
                data,
                borderColor: '#ec2146',
                pointBorderWidth: 3,
                pointHoverBorderWidth: 5,
                pointBorderColor: '#fff',
                tension: 0,
                fill: true,
              },
            ],
          },
          options: {
            scales: {
              y: {
                beginAtZero: true,
                min: minYValue,
                title: {
                  display: true,
                  text: 'Number of Connections',
                  font: {
                    size: 16,
                    weight: 'bold',
                  },
                },
                grid: {
                  color: "#716a6b94",
                },
              },
              x: {
                title: {
                  display: true,
                  text: 'Months',
                  font: {
                    size: 16,
                    weight: 'bold',
                  },
                },
                grid: {
                  color: '#716a6b94',
                },
              },
            },
            animation: {
              duration: 2000,
              easing: 'linear',
            },
          },
        });

        setChartInstance(newChartInstance);
      } catch (error) {
        console.error('Error fetching or processing data:', error);
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    const handleResize = () => {
      chartRef.current.height = window.matchMedia('(max-width: 998px)').matches ? 200 : 120;
    };

    handleResize();

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return <canvas ref={chartRef} />;
};

export default ChartLink;
